.jumbotron{
  background-color: rgb(0, 247, 255);
   width:100%;
   height:300px;
   padding-top: 0;
   padding-left: 0;
}
img{
   width:100%;
   height:300px;
}
.header{
   padding: 3%;
   color:white;
}

#head{
  font-family: 'Permanent Marker', cursive;
}
@media screen and (max-width: 640px){
   #instructions{
       font-size:20px;
   }
   #head{
       font-size: 32px;
   }
   .jumbotron{
       height:250px;
   }
   img{
       height:250px;
   }
   .header{
       padding:1%;
   }
}
.score{
  color:white;
  background-color:rgb(0, 162, 255);
  padding:2%;
  font-size: 30px;
  font-family: 'Permanent Marker', cursive;

}
