  background: #fff;
  border-radius: 2px;
  height: 120px;
  margin: 1rem;
  position: relative;
  width: 15%;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  float: left;
}
.imgclass{
  width:100%;
  height:120px;
}
